import React, { useState, useEffect } from "react";
import { Card, CardBody, CardTitle, Container, Row, Col } from "reactstrap";
import { useDispatch, useSelector } from "react-redux";
import { getXaridData } from "redux/action/xarid/tableActions";
const Header = () => {
  const dispatch = useDispatch();
  const { table } = useSelector((state) => state.xaridTable);

  useEffect(() => {
    dispatch(getXaridData());
  }, []);
  const lastItem = table[table.length - 1];
  //   const { amounttotal, amountpurchase, totalthatsall } = lastItem;
  return (
    <>
      <div className="header bg-gradient-info pb-8 pt-5 pt-md-8">
        <Container fluid>
          <div className="header-body">
            {/* Card stats */}
            <Row>
              <Col lg="4" xl="4">
                <Card className="card-stats mb-4 mb-xl-0">
                  <CardBody>
                    <Row>
                      <div className="col">
                        <CardTitle
                          tag="h5"
                          className="text-uppercase text-muted mb-0"
                        >
                          Жами маҳсулот миқдори
                        </CardTitle>
                      </div>
                    </Row>
                    <div className="text-center mt-2 text-primary">
                      <span
                        style={{
                          fontSize: "26px",
                          fontWeight: 600,
                          fontFamily: "sans-serif",
                        }}
                      >
                        {lastItem?.amounttotal || 0}
                      </span>
                    </div>
                  </CardBody>
                </Card>
              </Col>
              <Col lg="4" xl="4">
                <Card className="card-stats mb-4 mb-xl-0">
                  <CardBody>
                    <Row>
                      <div className="col">
                        <CardTitle
                          tag="h5"
                          className="text-uppercase text-muted mb-0"
                        >
                          Сотиб олиниш суммаси
                        </CardTitle>
                      </div>
                    </Row>
                    <div className="text-center mt-2 text-primary">
                      <span
                        style={{
                          fontSize: "26px",
                          fontWeight: 600,
                          fontFamily: "sans-serif",
                        }}
                      >
                        {lastItem?.amountpurchase || 0}
                      </span>
                    </div>
                  </CardBody>
                </Card>
              </Col>
              <Col lg="4" xl="4">
                <Card className="card-stats mb-4 mb-xl-0">
                  <CardBody>
                    <Row>
                      <div className="col">
                        <CardTitle
                          tag="h5"
                          className="text-uppercase text-muted mb-0"
                        >
                          Жами таннарх суммаси
                        </CardTitle>
                      </div>
                    </Row>
                    <div className="text-center mt-2 text-primary">
                      <span
                        style={{
                          fontSize: "26px",
                          fontWeight: 600,
                          fontFamily: "sans-serif",
                        }}
                      >
                        {lastItem?.totalthatsall || 0}
                      </span>
                    </div>
                  </CardBody>
                </Card>
              </Col>
            </Row>
          </div>
        </Container>
      </div>
    </>
  );
};

export default Header;
