import {POST_SELL_LIST,FAIL_SELL} from '../../typeSell'
import axios from 'axios'

export const postSellData = (e) => async (dispatch) => {
      try {
          
          const {data} = await axios.post('http://blmoliya.uz/api/shopping/shop/',e)
          dispatch({
              type: POST_SELL_LIST,
              payload: data
          })
        
   
      } catch (error) {
          dispatch({
              type: FAIL_SELL,
              payload: error.response && error.response.data.detail
                  ? error.response.data.detail
                  : error.message,
          })
      }
  }