import {
      GET_SELL_TABLE,
      DELETE_SELL_TABLE
      
    } from "../../typeSell"    
    const intialState = {
     sellTable: [],
    };
    
    export const sellTableReducer = (state = intialState, action) => {
      switch (action.type) {
        case GET_SELL_TABLE:
          return {
            ...state,
            sellTable: action.payload,
          };
        case DELETE_SELL_TABLE:
          return {
            ...state,
            sellTable: state.sellTable.filter(
              (contact) => contact.id !== action.payload
            ),
          };
        default:
          return state;
      }
    };
    