import React, { useEffect, useState } from 'react';
import { Button, Card, CardBody, Col, FormGroup, Form, Input, InputGroup, Row ,Table} from 'reactstrap';
import { useDispatch,useSelector} from 'react-redux';
import { MdDelete } from 'react-icons/md';
import { productData,getProoductData } from '../../redux/action/xarid/productName';
function InputFirstData() {
	const [name, setName] = useState('');
	const dispatch = useDispatch();
	const {product} = useSelector(state=>state.product)
	useEffect(()=>{
		async function getData(){
			await dispatch(getProoductData())
		}
		getData()

	},[])
	
	const postName = async (e) => {
		e.preventDefault()
		dispatch(productData(name));
		setName('');
	};
	// const lastItem = data[data.length - 1];
	return (
		<>
			<Row className="mt-4">
				<Col lg="12" md="12" style={{ marginBottom: 10 }}>
					<Card className="shadow border-0" style={{ backgroundColor: '#E5F0FF' }}>
						<CardBody className="px-lg-5 py-lg-3">
							<div className="text-center text-muted mb-4">
								<small
									style={{
										fontSize: '16px',
										color: 'gray',
										fontWeight: '500',
										textAlign: 'center',
										marginTop: '10px'
									}}
								>
									Маҳсулот номини қушиш
								</small>
							</div>

							<Form role="form" onSubmit={postName}>
								<Row>
									<Col lg={4}>
										<FormGroup>
											<InputGroup className="input-group-alternative mb-3">
												<Input
													placeholder="Маҳсулот"
													type="text"
													autoComplete="new-hisob"
													value={name}
													onChange={(e) => setName(e.target.value)}
												/>
												<Button
													type="submit"
													style={{ background: '#5E72E4', border: 'none' }}
												>
													<i class="fas fa-plus"></i>
												</Button>
											</InputGroup>
										</FormGroup>
									</Col>
									
								</Row>
							</Form>
							<Card>
							<Table className="align-items-center table-flush" responsive>
							<thead className="thead-light">
								<tr>
									<th scope="col">Маҳсулот номи</th>
									<th scope="col">Тахрирлаш</th>
								</tr>
							</thead>
							<tbody>
								{product?.map((item, index) => {
									return (
										<>
											 
												<tr key={index}>
													<th scope="row">{item.name}</th>
													<td
														style={{
															display: 'flex',
															alignItems: 'center',
															justifyContent: 'start',
															textAlign: 'left'
														}}
													>
														{/* <ModalKont id={item.id} /> */}
														<MdDelete
															style={{
																fontSize: '20px',
																cursor: 'pointer',
																color: 'red'
															}}
															onClick={() => ""}
														/>
													</td>
												</tr>
											
										</>
									);
								})}
							</tbody>
						</Table>
							</Card>
							
						</CardBody>
					</Card>
				</Col>
			</Row>
		</>
	);
}

export default InputFirstData;
