import React, { useState, useEffect } from "react";
import {
  Button,
  Modal,
  ModalHeader,
  ModalBody,
  ModalFooter,
  InputGroup,
  Input,
  Card,
  CardBody,
  FormGroup,
  Form,
  Row,
  Col,
  Label,
} from "reactstrap";
import { FormControlLabel, Switch } from "@material-ui/core";
import { withStyles } from "@material-ui/core/styles";
import Select from "react-select";
import { useDispatch, useSelector } from "react-redux";
import { getKontData } from "../../../../redux/action/kassa/kontAction";
import { getAcData } from "../../../../redux/action/kassa/acAction";
import { getFuraData } from "redux/action/xarid/furaAction";
import { getProoductData } from "redux/action/xarid/productName";
import { postSellData } from "redux/action/sell/sellInputAction";
const IOSSwitch = withStyles((theme) => ({
  root: {
    width: 42,
    height: 26,
    padding: 0,
    margin: theme.spacing(1),
  },
  switchBase: {
    padding: 1,
    "&$checked": {
      transform: "translateX(16px)",
      color: theme.palette.common.white,
      "& + $track": {
        backgroundColor: "#52d869",
        opacity: 1,
        border: "none",
      },
    },
    "&$focusVisible $thumb": {
      color: "#52d869",
      border: "6px solid #fff",
    },
  },
  thumb: {
    width: 24,
    height: 24,
  },
  track: {
    borderRadius: 26 / 2,
    border: `1px solid ${theme.palette.grey[400]}`,
    backgroundColor: theme.palette.grey[50],
    opacity: 1,
    transition: theme.transitions.create(["background-color", "border"]),
  },
  checked: {},
  focusVisible: {},
}))(({ classes, ...props }) => {
  return (
    <Switch
      focusVisibleClassName={classes.focusVisible}
      disableRipple
      classes={{
        root: classes.root,
        switchBase: classes.switchBase,
        thumb: classes.thumb,
        track: classes.track,
        checked: classes.checked,
      }}
      {...props}
    />
  );
});
const createOption = (label, value) => ({
  label,
  value,
});
const ModalExample = (props) => {
  const dispatch = useDispatch();
  const { kont } = useSelector((state) => state.kont);
  const { ac } = useSelector((state) => state.account);
  const { fura } = useSelector((state) => state.fura);
  const { product } = useSelector((state) => state.product);
  const defaultOption2 = kont.map((e) => createOption(e.name, e.id));
  const [konts, setKonts] = useState({
    isLoading: false,
    options: [...defaultOption2],
    value: undefined,
  });
  const { value } = konts;
  const defaultOption = ac.map((e) => createOption(e.name, e.id));
  const [acc, setAcc] = useState({
    options1: [...defaultOption],
    value1: undefined,
  });
  const { value1 } = acc;
  const defaultOption1 = fura.map((e) => createOption(e.name, e.id));
  const [furaa, setFura] = useState({
    options2: [...defaultOption1],
    value2: undefined,
  });
  const { value2 } = furaa;
  const defaultOption3 = product.map((e) => createOption(e.name, e.id));
  const [products, setProduct] = useState({
    options3: [...defaultOption1],
    value3: undefined,
  });
  const { value3 } = products;

  useEffect(() => {
    const getKont = async () => {
      dispatch(getKontData());
      dispatch(getAcData());
      dispatch(getFuraData());
      dispatch(getProoductData());
    };
    getKont();
  }, []);

  const [purNumber, setPurNumber] = useState("");
  const [date, setDate] = useState("");
  const [date2, setDate2] = useState("");
  const [phone, setPhone] = useState("");
  const [buyPrice, setBuyPrice] = useState("");
  const [amount, setAmount] = useState("");
  const [moneyCome, setMoneyCome] = useState("");
  const [sellPrice, setSellPrice] = useState("");
  const [month, setMonth] = useState("");
  const [date1, setDate1] = useState("");
  const [modal, setModal] = useState(false);
  const [expence, setExpence] = useState("");
  const [toggle1, setToggle1] = useState(false);
  const toggle = () => setModal(!modal);
  const postSell = () => {
    const dataXarid = {
      furaName: purNumber,
      sana: date,
      countr: value?.value,
      telnumber: phone,
      paymentType: value1?.value,
      paymentLifeTime: date1,
      commodityType: value3?.value,
      miqdori: amount,
      sotishNarxi: sellPrice,
      olishNarxi: buyPrice,
      paymentDate: date2,
      paymentFinish: moneyCome,
      month: month,
      retroBonusApi: toggle1 ? "True" : "False",
      expense: expence ? expence : 0,
    };

    dispatch(postSellData(dataXarid));
    setPurNumber("");
    setPhone("");
    setDate("");

    setAmount("");
    setBuyPrice("");

    setDate1("");
    setProduct({ value3: "" });
    setFura({ value2: "" });
    setKonts({ value: "" });
    setAcc({ value1: "" });
  };

  return (
    <div>
      <Button color="danger" onClick={toggle} style={{ width: "100%" }}>
        Сотув
      </Button>
      <Modal
        isOpen={modal}
        toggle={toggle}
        size="lg"
        style={{ maxWidth: "1600px", width: "80%", margin: "10px auto" }}
      >
        <ModalHeader toggle={toggle}>Сотув</ModalHeader>
        <ModalBody>
          <Card
            className="shadow border-0"
            style={{ backgroundColor: "#E5F0FF" }}
          >
            <CardBody className="px-lg-5 py-lg-3">
              <Form role="form" name="google-sheet" method="POST">
                <Row>
                  <Col lg="6" xl="6">
                    <FormGroup>
                      <InputGroup className="input-group-alternative mb-3">
                        <Input
                          placeholder="Фуралар номери"
                          type="text"
                          value={purNumber}
                          onChange={(e) => setPurNumber(e.target.value)}
                        />
                      </InputGroup>
                    </FormGroup>
                    <FormGroup>
                      <InputGroup className="input-group-alternative mb-3">
                        <Input
                          placeholder="Сана"
                          type="date"
                          value={date}
                          onChange={(e) => setDate(e.target.value)}
                        />
                      </InputGroup>
                    </FormGroup>
                    <FormGroup>
                      <Select
                        placeholder={"Контрагент"}
                        isClearable
                        onChange={(e) => setKonts({ value: e })}
                        options={defaultOption2}
                        value={value}
                      />
                    </FormGroup>
                    <FormGroup>
                      <InputGroup className="input-group-alternative mb-3">
                        <Input
                          placeholder="Телефон рақам"
                          type="text"
                          value={phone}
                          onChange={(e) => setPhone(e.target.value)}
                        />
                      </InputGroup>
                    </FormGroup>
                    <FormGroup>
                      <Select
                        placeholder={"Маҳсулот номи"}
                        isClearable
                        value={value3}
                        options={defaultOption3}
                        onChange={(e) => setProduct({ value3: e })}
                      />
                    </FormGroup>
                    <FormGroup>
                      <InputGroup className="input-group-alternative mb-3">
                        <Input
                          placeholder="Миқдори"
                          type="number"
                          value={amount}
                          onChange={(e) => setAmount(e.target.value)}
                        />
                      </InputGroup>
                    </FormGroup>
                    <FormGroup>
                      <InputGroup className="input-group-alternative mb-3">
                        <Input
                          placeholder="Сотиш нархи"
                          type="number"
                          value={sellPrice}
                          onChange={(e) => setSellPrice(e.target.value)}
                        />
                      </InputGroup>
                    </FormGroup>
                    <FormGroup>
                      <FormControlLabel
                        control={
                          <IOSSwitch
                            checked={toggle1}
                            onChange={() => setToggle1(!toggle1)}
                            name="checkedB"
                          />
                        }
                        label=""
                      />
                    </FormGroup>
                  </Col>
                  <Col lg="6" xl="6">
                    <FormGroup>
                      <InputGroup className="input-group-alternative mb-3">
                        <Input
                          placeholder="Сотиб олиш нархи"
                          type="number"
                          value={buyPrice}
                          onChange={(e) => setBuyPrice(e.target.value)}
                        />
                      </InputGroup>
                    </FormGroup>

                    <FormGroup>
                      <Select
                        placeholder={"Тўлов тури"}
                        isClearable
                        options={defaultOption}
                        value={value1}
                        onChange={(e) => setAcc({ value1: e })}
                      />
                    </FormGroup>

                    <FormGroup>
                      <InputGroup className="input-group-alternative mb-3">
                        <Input
                          placeholder="Пул бериш муддати"
                          type="text"
                          value={date1}
                          onChange={(e) => setDate1(e.target.value)}
                        />
                      </InputGroup>
                    </FormGroup>
                    <FormGroup style={{ position: "relative" }}>
                      <Label>
                        <span
                          style={{
                            backgroundColor: "",
                            position: "absolute",
                            top: 0,
                            fontSize: ".8rem",
                          }}
                        >
                          Тўлов санаси{" "}
                        </span>
                      </Label>
                      <InputGroup className="input-group-alternative mb-3">
                        <Input
                          placeholder=""
                          type="date"
                          value={date2}
                          onChange={(e) => setDate2(e.target.value)}
                        />
                      </InputGroup>
                    </FormGroup>
                    <FormGroup>
                      <InputGroup className="input-group-alternative mb-3">
                        <Input
                          placeholder="Пул келиб тушди"
                          type="text"
                          value={moneyCome}
                          onChange={(e) => setMoneyCome(e.target.value)}
                        />
                      </InputGroup>
                    </FormGroup>
                    <FormGroup>
                      <InputGroup className="input-group-alternative mb-3">
                        <Input
                          placeholder="Ой"
                          type="text"
                          value={month}
                          onChange={(e) => setMonth(e.target.value)}
                        />
                      </InputGroup>
                    </FormGroup>
                    <FormGroup>
                      <InputGroup className="input-group-alternative mb-3">
                        <Input
                          placeholder="Пул ечиш"
                          type="text"
                          value={expence}
                          onChange={(e) => setExpence(e.target.value)}
                        />
                      </InputGroup>
                    </FormGroup>
                  </Col>
                </Row>

                <div className="text-right">
                  <Button className="mt-4" color="primary" onClick={postSell}>
                    Jadvalga qo'shish
                  </Button>
                </div>
              </Form>
            </CardBody>
          </Card>
        </ModalBody>
        <ModalFooter></ModalFooter>
      </Modal>
    </div>
  );
};

export default ModalExample;
