import {GET_SELL_TABLE,FAIL_SELL_TABLE,DELETE_SELL_TABLE} from '../../typeSell'
import axios from 'axios'

export const getSellData = (e) => async (dispatch) => {
      try {
          const {data} = await axios.get('http://blmoliya.uz/api/shopping/shop/')
          dispatch({
              type: GET_SELL_TABLE,
              payload: data
          }) 
      } catch (error) {
          dispatch({
              type: FAIL_SELL_TABLE,
              payload: error.response && error.response.data.detail
                  ? error.response.data.detail
                  : error.message,
          })
      }
  }

  export const deleteSellTable =(id)=>async dispatch =>{
    try {
      dispatch({
        type: DELETE_SELL_TABLE,
        payload: id,
      })
  
   
      const {data}= await axios.delete(`blmoliya.uz/api/shopping/shop/${id}`)  
  
    } catch(error){
      console.log(error)
    }
  }