import { Card, CardHeader, CardBody, Row } from "reactstrap";
import React from "react";

import Chart from "react-apexcharts";
import Data from "../../../../variables/data";

const createOption = (x) => x.replaceAll(",", "");

const VerticalBar = () => {
  const datas = Data();
  const defaultOption = datas.map((e) => createOption(e.sfsum));
  const defaultOption1 = datas.map((e) => createOption(e.sfsana));

  const data = {
    series: [
      {
        name: "Сумма",
        data: [4, 3, 6, 7, 10, 6, 8, 9, 1, 11],
      },
    ],
    options: {
      chart: {
        type: "bar",
        height: 350,
      },
      plotOptions: {
        bar: {
          colors: {
            ranges: [
              {
                from: -100,
                to: -46,
                color: "#F15B46",
              },
              {
                from: -45,
                to: 0,
                color: "#FEB019",
              },
            ],
          },
          columnWidth: "80%",
        },
      },
      dataLabels: {
        enabled: false,
      },
      yaxis: {
        title: {
          text: "Сумма",
        },
        labels: {
          formatter: function (y) {
            return y.toFixed(0) + "";
          },
        },
      },
      xaxis: {
        type: "text",
        categories: [
          "Alisher",
          "Ahmad",
          "Bahodir",
          "Abdulloh",
          "Abubakir",
          "Shohruh",
          "Asilbek",
          "Abduaziz",
          "Normat",
          "Abdulla",
        ],
        labels: {
          rotate: -90,
        },
      },
    },
  };
  return (
    <>
      <Card className="shadow">
        <CardHeader className="bg-transparent">
          <Row className="align-items-center">
            <div className="col">
              <h6 className="text-uppercase text-gray ls-1 mb-1">
                Фаол мижозлар
              </h6>
            </div>
          </Row>
        </CardHeader>
        <CardBody style={{ overflowX: "scroll" }}>
          {/* Chart */}
          <div className="chart" style={{ minWidth: 500, width: "100%" }}>
            <Chart
              options={data.options}
              series={data.series}
              type="bar"
              height={350}
            />
          </div>
        </CardBody>
      </Card>
    </>
  );
};

export default VerticalBar;
