import React, { useEffect } from "react";
import { useSelector, useDispatch } from "react-redux";
import { Card, CardHeader, Table, Row, Col } from "reactstrap";
import ReactHTMLTableToExcel from "react-html-table-to-excel";
import { getSellData } from "redux/action/sell/sellTableAction";
import SellRow from "./SellRow";
const KassaTable = () => {
  const dispatch = useDispatch();
  const { sellTable } = useSelector((state) => state.sell);

  useEffect(() => {
    dispatch(getSellData());
  }, []);

  return (
    <Row className="mt-5">
      <Col xl="12">
        <Card className="shadow" style={{ height: "100vh", padding: 10 }}>
          <CardHeader className="border-0">
            <Row className="align-items-center">
              <div className="col">
                <h3 className="mb-0">Жадвал</h3>
              </div>
              <div className="col text-right">
                <ReactHTMLTableToExcel
                  id="test-table-xls-button"
                  className="download-table-xls-button excel"
                  table="table-to-xls"
                  filename="Xarid table"
                  sheet="tablexls"
                  buttonText={"Excelga saqlash"}
                />
              </div>
            </Row>
          </CardHeader>
          <Table
            id="table-to-xls"
            className="align-items-center table-flush"
            responsive
          >
            <thead className="thead-light">
              <tr>
                <th>Фуралар номи</th>
                <th>№</th>
                <th>Сана</th>
                <th>Контрагент</th>
                <th>Телефон рақам</th>
                {/* <th>Гўшт тури</th> */}
                <th>Тўлов тури</th>
                <th>Пул бериш муддати</th>
                <th>Маҳсулот тури</th>
                <th>Миқдори</th>
                <th>Сотиш нархи</th>
                <th>Жами</th>
                <th>Сотиб олиш нархи</th>
                <th>Тўлов санаси </th>
                <th> Пул келиб тушди</th>
                <th> Қолдиқ</th>
                <th>Ой</th>
                <th>Ялпи фойда</th>
                <th>Ретро бонус</th>
                <th>Оператив фойда</th>
                <th>Пул ечиш</th>
                <th>Соф фойда</th>
                <th> 1 кгдан ялпи фойда</th>
                <th>1 кгдан оператив фойда</th>
                <th>1 кгдан соф фойда</th>
                <th>Ичик НДС</th>
                <th>Соф фойда солиғи</th>
                <th>Охирги фойда</th>
                <th>Сотув ҳолати</th>
                <th>Хафта</th>
                <th>Харид суммаси</th>
              </tr>
            </thead>
            <tbody>
              {sellTable.map((item, index) => (
                <SellRow row={item} index={index} />
              ))}
            </tbody>
          </Table>
        </Card>
      </Col>
    </Row>
  );
};

export default KassaTable;
